import React from "react"
import styled from "@emotion/styled"
import Layout from "../components/layout"
import SEO from "../components/seo"

const WordBlog = styled.h1`
  color: #677CE2;
`

const Para = styled.p`
font-size: 1rem;
`
const Content = styled.div`
  max-width: 860px;
  padding: 1rem 1.0875rem;
  font-size: 1.2rem;`

  const ALink = styled.a`
  color: #677CE2;
  text-decoration: none;
  display: inline-block;
  position: relative;

  ::after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #677CE2;
    transform-origin: bottom left;
    transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
  }

  :hover::after {
    transform: scaleX(1);
    transform-origin: bottom right;
  }
`

const Projects = () => (
  <Layout>
    <SEO title="Projects" keywords={[`designtack`, `thevediwho`, `sillymovie`]} />
    <WordBlog>Projects</WordBlog>
    <Content>
    <ul>
        <li><ALink href="http://sillymovie.designtack.com" target="_blank">Silly Movie Suggestor</ALink></li>
        <Para>This is a silly, kind of an app named #SillyMovieSuggestor as it 
          suggests you movies based on a random word or a word which you input and 
          it will show you movies containing that exact word.
          <br/>This was my First launch in <ALink href="https://www.producthunt.com/posts/silly-movie-suggestor">Producthunt</ALink> ðŸ¥³. (Not my First product :)</Para>
    
        <li><ALink href="/">Coming soon</ALink></li>
        <Para>An amazing resource!</Para>

        <li><ALink href="/">Coming soon</ALink></li>
        <Para>Lifestyle Problem Solver!</Para>
    </ul>
    </Content>
  </Layout>
)

export default Projects